<template>
  <v-card style="background-color: transparent" elevation="0" :disabled="loading">
    <v-row>
      <v-col v-if="$route.name!=='RefereeProjects'" class="d-flex flex-wrap py-0" cols="12">
        <v-btn v-if="$store.state.permissions && $store.state.permissions['create-product']" :to="{name:'AddProject'}" class="dash-btns dash-btns--blue mb-2 mx-2" height="32" dark>

          <v-icon class="ml-1" small>mdi-plus</v-icon>
          پروژه جدید

        </v-btn>
        <v-btn v-if="$route.name==='DashProjects'" :to="{name: 'DashProductCategory'}" class="dash-btns dash-btns--blue mb-2 mx-2" height="32" dark >
          <v-icon small class="ml-1" >mdi-file-tree</v-icon>
          دسته بندی ها
        </v-btn>

        <v-btn v-if="$route.name==='DashMyProjects'" @click="stateData.dialog = !stateData.dialog" class="dash-btns dash-btns--blue mb-2 mx-2" height="32" dark >
          <v-icon small class="ml-1" >mdi-plus</v-icon>
          نیازمندی - نوع حالت
        </v-btn>

        <v-btn v-if="$route.name==='DashMyProjects'" :to="{name:'DashAddStates'}" class="dash-btns dash-btns--blue mb-2 mx-2" height="32" dark >
          <v-icon small class="ml-1" >mdi-plus</v-icon>
          حالت جدید
        </v-btn>
      </v-col>
      <v-col md="12" cols="12">
        <BlockItem>

          <template slot="icon">mdi-google-nearby</template>
          <span v-if="$route.name==='DashMyProjects'" slot="title">پروژه های من</span>
          <span v-else-if="$route.name==='RefereeProjects'" slot="title">داوری پروژه ها</span>
          <span v-else slot="title">تمامی پروژه ها</span>


          <v-row v-if="$route.name==='DashProjects'" class="px-3 pb-3">
            <v-text-field
                v-model="searchVal"
                class="pt-0"
                color="txtf"
                style="max-width: 200px"
                label="جستجو"
                @keypress.enter="searchP()"
            >

              <template v-slot:append>
                <v-icon @click="searchP()">mdi-magnify</v-icon>
              </template>

            </v-text-field>
          </v-row>


          <v-data-table
              hide-default-footer
              :headers="headers" :loading="loading" :items="data.data" item-key="id" disable-sort
          >

            <template v-slot:[`item.title`]="{item}">
              <a class="secondary--text" @click="detail(item.id)"
                 v-if="item.title.length>50">{{ item.title.substring(0, 50) + '...' }}</a>
              <a class="secondary--text" @click="detail(item.id)" v-else>{{ item.title }}</a>
            </template>

            <template v-slot:[`item.owner.username`]="{item}">
              <a class="secondary--text" @click="toSeeProfile(item.owner.id)" >{{ item.owner.username }}</a>
            </template>

            <template v-slot:[`item.time`]="{item}">
              <span style="font-size: 12px">ش {{ item.date_const_date | momentDate }}</span>
              <br>
              <span style="font-size: 12px"> پ {{ item.date_expire_date | momentDate }}</span>
            </template>

            <template v-slot:[`item.price`]="{item}">
              <span style="font-size: 12px">{{ item.price | digit }} تومان</span>
            </template>


            <template v-if="$route.name!=='RefereeProjects'" v-slot:[`item.id`]="{item}">
              <div class="d-flex justify-center">
                <a-sbtn color="#1cbb8c"
                        @clickBtn="toStates(item)"
                        icon="mdi-bookmark-plus-outline" alt="حالت ها"/>
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['change-product']" :to="{name:'AddProject',params:{id:item.id}}" color="#17a2b8" icon="mdi-pencil"
                        alt="ویرایش پروژه"/>
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['delete-product']" @clickBtn="toDelete(item.id,'/projects/api/v1/product/delete-product/')" color="#DC3545"
                        icon="mdi-delete" alt="حذف پروژه"/>
              </div>
            </template>

            <template v-else v-slot:[`item.id`]="{item}">
              <div class="d-flex justify-center">

                <a-sbtn :to="{name:'ProductDesigners',params:{id:item.id,title:item.title}}" color="#17a2b8"
                        icon="mdi-playlist-check" alt="لیست درخواست"/>
              </div>
            </template>


          </v-data-table>


          <v-pagination
              v-if="data.total_objects"
              :value="options.page"
              v-model="options.page"
              color="info"
              class="mt-6"
              :length="parseInt(data.total_pages)"
          ></v-pagination>
          <p class="text-center  mb-2 mt-2 info--text" v-if="data.total_objects" style="font-size: 14px">
            {{ data.total_objects }} پروژه</p>


        </BlockItem>
      </v-col>
    </v-row>
    <DeleteDialog :ddata="deleteData" @deleteFn="fetchRoute()"></DeleteDialog>
    <ProductSingleDialog :public-use="true" :ddata="product"/>
    <SingleProductStates @updatedP="fetchRoute()" :cdata="reqsData" ></SingleProductStates>
    <ProfileDialog :ddata="profileData" />
    <StatesDialog :ddata="stateData" />
  </v-card>
</template>

<script>
import axios from "axios";
import BlockItem from "@/components/Misc/BlockItem";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";
import ProductSingleDialog from "@/components/Misc/ProductSingleDialog";
import SingleProductStates from "@/components/Dashboard/DashProjects/SingleProductStates";
import ProfileDialog from "@/components/Misc/ProfileDialog";
import StatesDialog from "@/components/Dashboard/DashProjects/StatesDialog";

export default {
  name: "DashProjects",
  components: {
    StatesDialog,
    ProfileDialog,
    SingleProductStates,
    ProductSingleDialog,
    ASbtn,
    DeleteDialog,
    BlockItem
  },
  created() {
    this.fetchRoute();
  },
  data() {
    return {
      stateData : {
        dialog : false,
      },
      profileData : {
        idSelected : null,
        dialog : false,
      },
      searchVal: null,
      options: {
        page: 1,
        search: null,
        ordering: '-id'
      },
      reqsData : {
        dialog: false,
        loading: false,
        idSelected: null,
        type : false,
      },
      product: {
        api: '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      loading: false,
      data: {
        data: []
      },
      deleteData: {
        api: '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
    }
  },
  computed: {
    headers() {
      return [
        {
          text: 'عنوان پروژه',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'مالک', class: 'header-table-gray',
          align: 'center',
          value: 'owner.username',
        },
        {
          text: 'موقعیت', class: 'header-table-gray',
          align: 'center',
          value: 'location.name',
        }, {
          text: 'نوع', class: 'header-table-gray',
          align: 'center',
          value: 'product_type.title',
        }, {
          text: 'قیمت', class: 'header-table-gray',
          align: 'center',
          value: 'price',
        }, {
          text: 'زمان', class: 'header-table-gray',
          align: 'center',
          value: 'time',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'id',
        },


      ]
    },
  },
  methods: {
    toSeeProfile (id) {
      this.profileData.idSelected = id;
      this.profileData.dialog = true;
    },
    toStates(item) {
      this.reqsData.idSelected = null
      this.reqsData.idSelected = {...item};
      this.reqsData.dialog = true;
    },
    searchP() {
      this.options.search = this.searchVal;
      if (this.options.page === 1) {
        this.fetchRoute();
      } else {
        this.options.page = 1;
      }

    },
    detail(id) {
      this.product.dialog = true;
      this.product.idSelected = id;
    },
    fetchRoute() {
      if (this.$route.name === 'DashProjects') {
        this.fetchProjects('products');
      } else if (this.$route.name === 'DashMyProjects') {
        this.fetchProjects('owner-products');
      } else if (this.$route.name === 'RefereeProjects') {
        this.fetchProjects('referee-products');
      }
    },
    toDelete(id, api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },

    fetchProjects(string) {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/product/' + string, {
        params: this.options,
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.data = response.data;
        this.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    }
  },
  watch: {
    '$route.name'() {
      this.options = {
        page: 1,
        search: null,
        ordering: '-id'
      }
      this.searchVal = null;
      this.fetchRoute()
    },
    'options.page'() {
      this.fetchRoute();
    }
  }
}
</script>

