<template>
  <v-card elevation="0" >
    <v-form ref="form">
      <v-row class="pa-3" >
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4" >
            <v-text-field
                outlined
                dense
                :rules="[$store.state.rules.required(name,'عنوان')]"
                color="txtf"
                label="عنوان"
                v-model="name"
            >

            </v-text-field>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4" >
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(desc,'توضیحات')]"
              color="txtf"
              label="توضیحات"
              v-model="desc"
          >

          </v-text-field>
        </v-col>
        <v-col class="py-0" cols="2" md="2" >
        <v-btn v-if="!id && ($store.state.permissions && $store.state.permissions['create-requirement'])" dark class="dash-btns dash-btns--blue" height="39" :disabled="loading" @click="addTo()">
          + افزودن
        </v-btn>
          <div class="d-flex" v-else >
          <v-btn text dark @click="editTo()" color="#17a2b8" >
            <v-icon  color="#17a2b8" :disabled="loading"  >mdi-check</v-icon>
            ویرایش
          </v-btn>
          <v-btn small height="36" text dark v-if="id" @click="cleanEdit()" color="red" >
            <v-icon  color="error" :disabled="loading"  >mdi-close</v-icon>
          </v-btn>
          </div>
        </v-col>
        <v-col class="py-0" cols="12" >
          <v-data-table
              :headers="headers" :loading="loading" :items="data.data" item-key="id" disable-sort
          >

            <template v-slot:[`item.id`]="{item}">
              <div class="d-flex justify-end">

                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['change-requirement']" @clickBtn="toEdit(item)" color="#17a2b8"
                        icon="mdi-pencil" alt="ویرایش"/>
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['delete-requirement']" @clickBtn="toDelete(item.id,'/projects/api/v1/requirement/delete-requirement/')" color="#DC3545"
                        icon="mdi-delete" alt="حذف "/>

              </div>
            </template>

          </v-data-table>
        </v-col>
      </v-row>
    </v-form>
    <DeleteDialog @deleteFn="fetchDatas()" :ddata="deleteData" />
  </v-card>
</template>

<script>
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";
import {forceRerender} from "@/components/Misc/forceRerender";
import axios from "axios";
import DeleteDialog from "@/components/Misc/DeleteDialog";
export default {
  name: "RequirementsApp",
  mixins : [forceRerender],
  components: {DeleteDialog, ASbtn},
  created() {
    this.fetchDatas();
  },
  data() {
    return {
      id : null,
      deleteData: {
        api: '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      name : null,
      desc : null,
      loading : false,
      data : {
        data : [],
      }
    }
  },
  methods : {
    cleanEdit(){
      this.id = null;
      this.name = null;
      this.desc = null;
      this.forceRerender();
    },
    toDelete(id, api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },

    toEdit(item) {
      this.name = item.title;
      this.desc = item.desc
      this.id = item.id;
    },

    addTo(){
      this.$refs.form.validate();
      if(this.$refs.form.validate()) {
        const data = {
          title : this.name,
          desc : this.desc,
        }
        this.name = null;
        this.desc = null;
        this.loading = true;
        this.forceRerender();
        axios.post(this.$store.state.host + '/projects/api/v1/requirement/create-requirement',data,{
          headers : {
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(()=>{
          this.notify('نیازمندی اضافه شد','info');
          this.fetchDatas();
        }).catch(error=>{
          this.errorHandle(error.response);
        }).finally(()=>{
          this.loading = false;
        })
      }
    },
    editTo() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        const data = {
          id : this.id,
          title : this.name,
          desc : this.desc,
        }
        this.id= null;
        this.name = null;
        this.desc = null;
        this.loading = true;
        this.forceRerender();
        axios.patch(this.$store.state.host + '/projects/api/v1/requirement/change-requirement/'+data.id, data, {

          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('ویرایش شد', 'warning');
          this.fetchDatas();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    fetchDatas(){
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/requirement/requirements',{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(response=>{
        this.data = response.data;
      }).catch(error =>{
        this.errorHandle(error.response);
      }).finally(()=>{
        this.loading = false;
      })
    }
  },
  computed: {
    headers() {
      return [
        {
          text: 'عنوان نیازمندی',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'توضیحات',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'desc',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'end',
          value: 'id',
        },


      ]
    },
  },
}
</script>

