<template>
  <v-dialog v-model="data.dialog" :persistent="data.loading" max-width="760px">
    <v-card :disabled="data.loading" :loading="data.loading" elevation="0">
      <a-card-title>جزئیات پروژه</a-card-title>
      <v-card-text class="pb-0">
        <ProductData :item="item" />
      </v-card-text>
      <v-card-actions>

        <div v-if="$store.state.profile.role === 'designer' && !publicUse" class="dir-ltrF">
          <v-form ref="form">
            <v-text-field outlined dense @keypress="isNumber($event)" hide-details
              :rules="[$store.state.rules.required(price, 'قیمت')]" v-model="price" label="قیمت پیشنهادی" color="txtf">
              <template v-slot:append>
                <img width="24" height="24" src="../../assets/icon-toman.svg" />
              </template>

            </v-text-field>
          </v-form>
        </div>

        <v-spacer></v-spacer>
        <v-btn v-if="$store.state.profile.role === 'designer' && !publicUse" height="36" dark @click="addProjectDesign()"
          color="#222e3c">
          درخواست پروژه
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";
import { isChars } from "@/components/Misc/isChars";
import axios from "axios";
import numeral from "numeral";
import ProductData from "@/components/Misc/ProductData";

export default {
  components: { ProductData, ACardTitle },
  props: ["ddata", "publicUse"],
  mixins: [isChars],
  data() {
    return {
      price: null,
      designerPrice: null,
      item: {},
      data: this.ddata,
    }
  },
  watch: {
    'data.dialog'(val) {
      if (!val) {
        this.item = null;
        this.price = null;
        this.designerPrice = null;
      } else {
        this.fetchProdcut();
      }
    },
    'price'(val) {
      if (this.price) {
        this.price = numeral(val).format('0,0');
        this.designerPrice = numeral(this.price).value();
      } else {
        this.designerPrice = null;
      }
    },
  },
  name: "ProductSingleDialog",
  methods: {
    addProjectDesign() {
      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        this.data.loading = true;
        axios.post(this.$store.state.host + '/projects/api/v1/productdesigner/create-productdesigner', {
          product: this.data.idSelected,
          price: this.designerPrice
        }, {
          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },
        }).then(() => {
          this.notify('درخواست شما ثبت شد.', 'info')
          this.$router.push({ name: 'AllRequestsProductDesigners' })
        }).catch(error => {
          this.errorHandle(error.response)
        }).finally(() => {
          this.data.loading = false;
        })
      }
    },
    fetchProdcut() {
      this.data.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/product/single-product/' + this.data.idSelected, {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.item = response.data.data;
      }).catch(error => {
        if (error.response.status == 401) {
          this.$router.push({ name: 'Login' });
        } else {
          this.errorHandle(error.response)
        }
      }).finally(() => {
        this.data.loading = false;
      })
    },
  }
}
</script>

<style scoped></style>