<template>
  <v-dialog
      v-model="data.dialog"
      :persistent="data.loading"
      max-width="500"
  >
    <v-card :disabled="data.loading" :loading="data.loading" class="pass-form">

      <a-card-title>
        <span>افزودن حالت</span>
      </a-card-title>

      <v-card-text class="pt-12 pb-6 pb-0">

        <v-form ref="form" v-if="renderComponent" >
          <v-row>
            <v-col class="py-0" cols="=12">
              <v-autocomplete
                  item-color="info"
                  outlined
                  append-icon="mdi-kodi"
                  v-model="data.idSelected.state"
                  return-object
                  multiple
                  :items="states"
                  :rules="[$store.state.rules.required(data.idSelected.state,'حالت پروژه')]"
                  item-text="title"
                  item-value="id"
                  dense
                  label="حالت طراحی"
                  color="txtf"

              >

              </v-autocomplete>
            </v-col>
          </v-row>
        </v-form>

      </v-card-text>

      <v-card-actions>

        <v-spacer></v-spacer>
        <v-btn
            height="36"
            dark
            @click="changeProductState()"
            color="#222e3c"
        >
          ثبت حالت
        </v-btn>
      </v-card-actions>
      <template v-slot:progress >
        <GDLoading/>
      </template>
    </v-card>
  </v-dialog>
</template>

<script>


import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";
import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import {forceRerender} from "@/components/Misc/forceRerender";
import {isChars} from "@/components/Misc/isChars";

export default {
  name: "SingleProductStates",
  props : ["cdata"],
  mixins : [forceRerender,isChars],
  data () {
    return {
      data : this.cdata,
      states : null,
    }
  },
  methods : {

    fetchState() {
      axios.get(this.$store.state.host + '/projects/api/v1/state/states',{
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response=>{
        this.states = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    changeProductState (){
        this.$refs.form.validate();
        const state = this.data.idSelected.state.map(item => item.id)
        if(this.$refs.form.validate()){
          this.data.loading = true;
          axios.patch(this.$store.state.host + '/projects/api/v1/product/change-product/'+this.data.idSelected.id , {
            state
          },{
            headers: {
              Accept: "application/json",
              Authorization: `Bearer ${this.$store.state.userId}`,
            },
          }).then(() => {
            this.notify('حالت شما ثبت شد','info')
            this.data.dialog = false;
            this.$emit('updatedP')
          }).catch(error => {
            this.errorHandle(error.response)
          }).finally(() => {
            this.data.loading = false;
          })
        }
    },
  },
  watch : {
    'data.dialog'(val){
      if(val) {
        this.fetchState();
      } else {
        this.data.idSelected.state = null;
        this.forceRerender();
      }
    }
  },

  components: { GDLoading, ACardTitle}
}
</script>

