<template>
  <v-dialog
      v-model="data.dialog"
      :persistent="loading"
      max-width="1000px"
  >
    <v-card :loading="loading">

      <v-tabs
          slider-color="info"
          icons-and-text
          show-arrows
          v-model="tab"
          background-color="transparent"
          color="info"
          grow
      >
        <v-tab
            v-for="(item,i) in items"
            :key="i+'pItmesg'"
        >
          {{ item.text }}
          <v-icon>{{ item.icon }}</v-icon>
        </v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab">
        <v-tab-item
            v-for="(item,i) in items"
            :key="i+'titemsss'"
        >
          <v-card
              flat
              elevation="0"
          >
            <div style="padding: 24px 4px">
              <StateTypes v-if="i===1" />
              <RequirementsApp v-if="i===0" />
            </div>
          </v-card>
        </v-tab-item>
      </v-tabs-items>

      <template v-slot:progress>
        <GDLoading/>
      </template>
    </v-card>
  </v-dialog>
</template>

<script>
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import StateTypes from "@/components/Dashboard/DashProjects/StateTypes";
import RequirementsApp from "@/components/Dashboard/DashProjects/Requirements";

export default {
  name: "StatesDialog",
  components: { RequirementsApp, StateTypes, GDLoading},
  props : ["ddata"],
  data () {
    return {
      loading : false,
      data : this.ddata,
      tab: null,
      items: [
        {text:'نیازمندی ها',icon : 'mdi-asterisk'},
        {text:'نوع حالت ها',icon : 'mdi-layers'},
      ],
    }
  },
  watch: {
    'data.dialog'(val) {
      if (!val) {
        // null kari
        this.loading = false;
      } else {
        //fetchKari
        this.loading = false;
      }
    },
  },
}
</script>

